import React from 'react';
import styled from 'styled-components';
import imagePlaceholder from '../images/gatsby-astronaut.png';
import '../styles/tweetcards.css';
import fakeTweets from '../assets/fake_tweets.json';
import TweetCard from '../components/TweetCard';

const TweetCardsDisolve = styled.div`
	width: 100%;
	height: 15px;
	position: absolute;
	top: 0;
	z-index: 2;
	background: linear-gradient(rgb(20, 22, 25), rgba(0, 0, 0, 0));
	display: none;
	@media (min-width: 768px) {
		display: block;
	}
`;

const TweetCardsDisolveReverse = styled.div`
	width: 100%;
	height: 15px;
	position: absolute;
	bottom: 0;
	z-index: 2;
	background: linear-gradient(rgba(0, 0, 0, 0), rgb(20, 22, 25));
	display: none;
	@media (min-width: 768px) {
		display: block;
	}
`;

const TweetCardsGrid = styled.div`
	padding: 1rem 0;
	margin: 0 auto;
	width: 100vw;
	display: grid;
	grid-template-columns: 1fr;
	justify-content: flex-start;
	align-items: flex-start;
	grid-gap: 1rem;
	position: relative;
	overflow-x: scroll;
	overflow-y: hidden;
	@media (min-width: 768px) {
		width: 100%;
		overflow-y: scroll;
		max-width: 700px;
		grid-template-columns: 1fr 1fr;
		height: 700px;
	}
`;
const TweetCards = () => {
	return (
		<div style={{ position: 'relative' }}>
			<TweetCardsDisolve />
			<TweetCardsGrid>
				<div className={`tweet-column`}>
					{fakeTweets.map(({ title, url, fullName }) => (
						<TweetCard
							tweet={title.repeat(Math.floor(Math.random() * 5) + 1).slice(0, 280)}
							thumb={url}
							name={fullName}
						/>
					))}
				</div>
				<div
					id={`second-tweet-col`}
					className={`tweet-column`}
					style={{ animationDirection: 'alternate-reverse' }}
				>
					{fakeTweets.map(({ title, url, fullName }) => (
						<TweetCard
							tweet={title.repeat(Math.floor(Math.random() * 5) + 1)}
							thumb={url}
							name={fullName}
						/>
					))}
				</div>
			</TweetCardsGrid>
			<TweetCardsDisolveReverse />
		</div>
	);
};

export default TweetCards;
